import React from 'react';
import './style.css';
import { connect } from 'react-redux';
import AppAction from '../../actions/app';
import route from '../../config/route';
import { Link, withRouter } from 'react-router-dom';
import {
    Dropdown,
} from 'react-bootstrap';
import language from '../../modules/language';

export function NavigationHolder(props) {
    return (
        <div className="navigation"
            style={{
                zIndex: '-1 !important',
            }}></div>
    );
}

class Navigation extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            menuItems: this.generateMenu(),
        }
    }

    generateMenu() {
        return [
            { title: language.t('home_page'), path: route.Home },
            // {title: language.t('about_page'), path: route.Intro},
            { title: language.t('investment_activity_page'), path: route.InvestmentActivity },
            { title: language.t('partner_page'), path: route.Partner },
            { title: language.t('project_page'), path: route.Project },
            { title: language.t('sustainable_development_page'), path: route.SustainableDevelopment },
            { title: language.t('resource_page'), path: route.Resource },
            { title: language.t('news_page'), path: route.News },
            { title: language.t('customer_service_page'), path: route.CustomerService },
            // {title: language.t('shopping_page'), path: route.Shopping},
            { title: language.t('contact_page'), path: route.Contact },
        ];
    }

    componentWillReceiveProps(nextProps) {
        const nextLocation = nextProps.location || {};
        const preLocation = this.props.location || {};
        if (nextLocation.pathname != preLocation.pathname) {
            this.props.hideMenu();
        }
        if (nextProps.app.currentLanguage != this.props.app.currentLanguage) {
            this.setState({
                menuItems: this.generateMenu(),
            })
        }
    }

    componentWillUnmount() {

    }
    toggleMenu = () => {
        if (this.props.app.showMenu) {
            this.props.hideMenu();
        } else {
            this.props.showMenu();
        }
    }

    changeLanguage = (item) => {
        this.props.changeLanguage(item);
    }

    render() {
        const { app } = this.props;
        return [
            <div key={'nav-1'} className="col-12 row mx-0 px-0
            justify-content-between align-items-center navigation position-fixed">
                {/**Left part */}
                <div className="navigation-logo-container row align-items-center mx-0">
                    <div className="navigation-logo-background">

                    </div>
                    <a className="navigation-logo"  href="/">
                        <img src={require('../../assets/images/g-g-logo.png')}
                            className="navigation-logo" />
                    </a>
                </div>

                {/**Middle part */}
                <div className="none-mobile">
                    <h3 className="active">{language.t('company_name')}</h3>
                </div>
                {/**Right part */}
                <div className="navigation-right-container row mx-0 h-100 justify-content-end align-items-center">
                    {/**Action */}
                    <div className="navigation-action-container row mx-0 align-items-center">
                        <Dropdown>
                            <Dropdown.Toggle style={{
                                backgroundColor: 'transparent',
                                borderColor: 'transparent',
                            }} >
                                <img src={app.currentLanguage.logo} style={{
                                    width: '1.5em',
                                    height: '1.25em',
                                    marginRight: '16px',
                                }} alt="language-logo" />
                                <span className="none-mobile text-dark">
                                    {app.currentLanguage.name}
                                </span>
                            </Dropdown.Toggle>

                            <Dropdown.Menu>
                                {app.supportedLanguages.map(item =>
                                    <Dropdown.Item key={item.code}
                                        onClick={() => this.changeLanguage(item)}>
                                        <img src={item.logo} style={{
                                            width: '1.5em',
                                            height: '1.25em',
                                            marginRight: '16px',
                                        }} alt="language-logo" />
                                        {item.name}
                                    </Dropdown.Item>
                                )}
                            </Dropdown.Menu>
                        </Dropdown>
                        <Link to={route.Home}>
                            <div className="none-mobile navigation-action-icon active mx-2">
                                <i className="fas fa-home"></i>
                            </div>
                        </Link>

                        <div className="navigation-action-icon mx-2">
                            <i className="fas fa-search"></i>
                        </div>

                    </div>
                    {/**End action */}
                    {/**Menu */}
                    <div className="navigation-action-menu" onClick={this.toggleMenu}>
                        <div className="navigation-action-menu-background">
                        </div>

                        <img src={require('../../assets/images/menu-normal.png')}
                            className="h-100 navigation-action-menu-icon" />
                    </div>
                    {/**End menu */}
                </div>
            </div>,
            <div key={'nav-2'} className={`position-fixed h-100 
            navigation-menu-overlay row mx-0 justify-content-end
             ${app.showMenu ? 'w-100' : ''}`}>
                <div className="h-100 navigation-menu-container">
                    {this.state.menuItems.map(item =>
                        <Link to={item.path} className="navigation-link">
                            <div className="w-100 py-3 px-4 navigation-menu-item">
                                {item.title}
                            </div>
                        </Link>
                    )}

                </div>
            </div>,
            // <div className="w-100 navigation"></div>
        ];
    }
}

const mapStateToProps = state => ({
    app: state.app,
});

const mapDispatchToProps = {
    showMenu: AppAction.showMenu,
    hideMenu: AppAction.hideMenu,
    changeLanguage: AppAction.changeLanguage,
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Navigation));