import React from 'react';
import PropTypes from 'prop-types';
import ItemsCarousel from 'react-items-carousel';
import './style.css';
import {Icon, Carousel} from 'antd';
import contactBanner from '../../assets/images/custom-service/contact-banner.jpg';
import TakeCare from './TakeCare';
import language from '../../modules/language';
import {connect} from 'react-redux';
import SpecialOffers from './SpecialOffers';
import CustomerGuide from './CustomerGuide';


class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      focus: false,
      activeItemIndex: 0,
      dropIndex: -1,
    }
    this.maxItem = 7;
  }

  setFocus(focus) {
    this.setState({ focus })
  }

  selectCategory(index) {
    if (index !== this.state.activeItemIndex) {
      this.setState({ activeItemIndex: index })
    }
  }

  selectDropContent(index){
    if(this.state.dropIndex !== index){
      this.setState({dropIndex: index});
    }
  }


  render() {
    
    const {  activeItemIndex, dropIndex } = this.state;
    const { data } = this.props;

    const categories = [
      language.t("customerService.Customer_care_switchboard.title"),
      "Chương trình ưu đãi",
      "Dịch vụ khách hàng"
    ]
    const dropdowns = [
      "Hướng dẫn thông tin khách hàng",
      "Quy trình tiếp nhận & phản hồi thông tin",
      "Quy trình xây dựng",
      "Ứng dụng CSKH"
    ]

    let dropContent = dropdowns.map((item, index)=>{
      return(
        <a key={index} class={index === this.state.dropIndex ? 'sub-nav-active' : ''} 
        onClick={()=>this.selectDropContent(index)}>
          {item}
        </a>
      )
    })

    const drop = (
      <div class="customService-dropdown-content">
        {dropContent}
      </div>
    )
    


    let cates = []
    for (let i = 0; i < categories.length; i++) {
      let active = "nav-item sub-nav-item ";
      if (i === activeItemIndex) active = active + ' sub-nav-active'
      
      cates.push(
        <li className={active + (i=== 2 ? " customService-dropdown" : '')} onClick={() => this.selectCategory(i)}>
            <a className="nav-link " >{categories[i]}</a>  
            {i===2 ? drop : null} 
        </li>
      )
    }
    return (
      <>
        <div>
          <div className="customService-contact">
            <ul>
              <li>
                <div className="customService-contact-item">
                <Icon style={{fontSize:'20px', color:"#285d3b"}} type="phone" />
                  <span> {language.t("customerService.hotline")}</span>
                  <p>19001979</p>
                </div>
              </li>
              <li>
                <div className="customService-contact-item">
                <Icon style={{fontSize:'20px', color:"#285d3b"}} type="mail" />
                  <span> {language.t("customerService.email")}</span>
                  <p>ggxaydung@gmail.com</p>
                </div>
              </li>
              <li>
                <div className="customService-contact-item">
                <Icon style={{fontSize:'20px', color:"#285d3b"}} type="clock-circle" />
                  <span> {language.t("customerService.work_time")}</span>
                  <p>08:00 - 17:00</p>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div
          className="col-12 row justify-content-center align-items-center sub-navigation"
          style={{ marginTop: "5px" }}>
          <ul className="nav">
            {cates}
          </ul>
         
        </div>
        <div className="customService-contact-banner">
            <Carousel autoplay>
              <div>
                <img src={contactBanner}/>
              </div>
            </Carousel>
        </div>
        {activeItemIndex === 0 ? <TakeCare /> 
          : activeItemIndex === 1 ? <SpecialOffers/>
          : activeItemIndex === 2 && (dropIndex === 0 || dropIndex === -1) ? <CustomerGuide/> : <TakeCare/>
          
        }
        
      </>
    )
  }

}

Page.propTypes = {
  setFocus: PropTypes.func
}

export default connect(state=>({app: state.app}),null)(Page);