import React, { createRef } from 'react';
import './style.css';
import { connect } from 'react-redux';
import language from '../../../modules/language';
import { Row, Col } from 'react-bootstrap';
// import { Icon } from 'antd';


class ContactForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      focus: false,
      name: "",
      email: "",
      phone: "",
      address: "",
      mess: "",
      errors: {

      }
    }
  }

  setFocus(focus) {
    this.setState({ focus });
  }

  submitForm = event => {
    event.preventDefault();
    const { name, email, phone, address, mess } = this.state;
    let regexEmail = /[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$/;
    let errors = {};
    if (!regexEmail.test(email)) {
      errors.email = "email chua dung dinh dang"
    }
    if (name === '') {
      errors.name = "sai ten"
    }
    if (phone === '') {
      errors.phone = 'sai dien thoaij'
    }
    if (address === '') {
      errors.address = "sai ten"
    }
    if (mess === '') {
      errors.mess = 'sai dien thoaij'
    }

    this.setState({ errors })
  }

  onInput(key, value) {
    let errors = this.state.errors;
    delete errors[key];
    this.setState({ [key]: value, errors})
  }


  render() {
    const focus = this.props.isFocus;
    const { name, email, phone, address, mess, errors } = this.state;

    return (
      <div
        style={{
          width: '100%',
          height: '100%',
          ...this.props.style,
        }}
      >
        <div className="contactForm-container">

          <h2
            style={{
              transition: 'all 2s',
              transitionDelay: '1s',
              opacity: focus ? 1 : 0,
              top : focus ? '0px' : '-40px',
              position: 'relative'
            }}
          >
            {language.t("contact.contact_form.title")}
          </h2>
          <div className="contact-form"
            style={{
              transition: 'all 2s',
              transitionDelay: '1s',
              opacity: focus ? 1 : 0,
              bottom: focus ? '0px' : '-40px',
              position: 'relative'
            }}
          
          >
            <form onSubmit={this.submitForm}>
              <Row>
                <Col className="text-left">
                  <input
                    className="contact-input-item"
                    onChange={(e) => this.onInput('name', e.target.value)}
                    type="text"
                    placeholder={language.t("contact.contact_form.name")}
                    value={name}
                  />
                  <span className="error"> {errors.name}</span>
                </Col>
                <Col className="text-left">
                  <input
                    className="contact-input-item"
                    onChange={(e) => this.onInput('email', e.target.value)}
                    type="text"
                    placeholder="Email(*)"
                    value={email}
                  />
                  <span className="error"> {errors.email}</span>
                </Col>
              </Row>
              <Row>
                <Col className="text-left">
                  <input
                    className="contact-input-item"
                    onChange={(e) => this.onInput('phone', e.target.value)}
                    type="text"
                    placeholder={language.t("contact.contact_form.phone")}
                    value={phone}
                  />
                  <span className="error"> {errors.phone}</span>
                </Col>
                <Col className="text-left">
                  <input
                    className="contact-input-item"
                    onChange={(e) => this.onInput('address', e.target.value)}
                    type="text"
                    placeholder={language.t("contact.contact_form.address")}
                    value={address}
                  />
                  <span className="error"> {errors.address}</span>
                </Col>
              </Row>
              <Row className="mx-0">
                <textarea
                  rows="4"
                  style={{ height: 'auto' }}
                  className="contact-input-item"
                  onChange={(e) => this.onInput('mess', e.target.value)}
                  type="text-area"
                  placeholder={language.t("contact.contact_form.mess")}
                  value={mess}
                />
                <span className="error"> {errors.mess}</span>
              </Row>
              <input
                className="contactForm-submit"
                type="Submit"
                value="Gửi thông tin"
              />
            </form>
          </div>
        </div>
      </div>
    )
  }
}


const mapStateToProps = state => ({
  currentLanguage: state.app.currentLanguage
})

export default connect(mapStateToProps, null)(ContactForm)