import React from 'react';
import PropTypes from 'prop-types';
import ItemsCarousel from 'react-items-carousel';
import './style.css';
import prev from '../../../assets/images/prev.png';
import next from '../../../assets/images/next.png';
import { connect } from 'react-redux';


import isoIcon from '../../../assets/images/quan-tri/iso-icon.jpg';
import leedIcon from '../../../assets/images/quan-tri/leed-icon.jpg';
import erpIcon from '../../../assets/images/quan-tri/erp-icon.jpg';
import bimIcon from '../../../assets/images/quan-tri/bim-icon.jpg';

class ImageLib extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            focus: false,
            activeItemIndex: 0,
        }
        this.maxItem = 7;
        this.data =
            [
                { url: bimIcon, title: 'BIM' },
                { url: isoIcon, title: 'ISO', },
                { url: erpIcon, title: 'ERP', },
                { url: leedIcon, title: 'LEED' }
            ]

        this.numberItem = Math.floor(window.innerWidth / 300);
        this.numberItem = this.numberItem < this.data.length ? this.numberItem : this.data.length;

    }

    componentDidMount() {
        this.interval = setInterval(() => {
            this.setState({
                activeItemIndex: this.state.activeItemIndex >= this.maxItem - 1 ?
                    0 : this.state.activeItemIndex + 1,
            });
        }, 15000);
    }

    setFocus(focus) {
        this.setState({ focus })
    }

    render() {
        const { activeItemIndex } = this.state;
        const data = this.data;
        const focus = this.props.isFocus;
        console.log("Item", this.numberItem)
        return (
            <div style={{
                ...this.props.style,
                backgroundPosition: "center",
                backgroundSize: "cover",
            }}>
                <div style={{
                    backgroundColor: "rgb(0,0,0,0.3)",
                    display: 'flex',
                    height: "100%",
                    width: "100%"
                }}>
                    <div className="lib-img-contain"
                        style={{
                            transition: 'all 2s',
                            position: 'relative',
                            bottom: focus ? '0' : '-50rem',
                            width: '95%'
                        }}
                    >
                        <h2>Hệ thống quản trị</h2>
                        <ItemsCarousel
                            requestToChangeActive={
                                (item) => {
                                    this.setState({ activeItemIndex: item })
                                }
                            }
                            activeItemIndex={activeItemIndex}
                            numberOfCards={this.numberItem}
                            gutter={20}
                            leftChevron={<div className="lib-chevron"
                            > <img width="50%" height="50%" style={{ margin: "auto" }} src={prev} /></div>}
                            rightChevron={<div className="lib-chevron"
                            > <img width="50%" height="50%" style={{ margin: "auto" }} src={next} /></div>}
                            slidesToScroll={1}
                        >
                            {data.map((item, index) =>
                                <div className="lib-img-item">
                                    <div className="row justify-content-center">
                                        <img src={item.url} style={{ width: 200, height: 200 }}></img>
                                    </div>
                                    <div
                                        style={{
                                            width: "100%",
                                            backgroundColor: "#fff",
                                            borderRadius: "10px",
                                            marginTop: "10px",
                                            color: "#000",
                                            textAlign: "center",
                                            padding: "10px"
                                        }}
                                    >
                                        <span>
                                            {item.title}
                                        </span>
                                    </div>
                                </div>
                            )}
                        </ItemsCarousel>
                    </div>
                </div>
            </div>
        )
    }

}

ImageLib.protoTypes = {
    setFocus: PropTypes.func
}

const mapStateToProps = state => ({
    app: state.app
})

export default connect(mapStateToProps, null)(ImageLib)