import React from 'react';
import _ from 'lodash';
import BoxGination from '../../components/box-pagination/box-pagination';
import AboutUs from './aboutUs/aboutUs';
import aboutUsBgd from '../../assets/images/introduction/about_us_bgrd.jpg';
import DevelopRoad from './developRoad/developRoad';
import developBgr from '../../assets/images/introduction/develop_road_bgr.jpg';
import realValBgr from '../../assets/images/introduction/real_val_bgr.jpg';
import managerBgr from '../../assets/images/introduction/manger_bgr.jpg';
import archieveBgr from '../../assets/images/introduction/archieve_bgr.jpg';
import RealValue from './realValue/realValue';
import StructCompany from './structCompany/structCompany';
import Archieve from './archievement/archieve';
import LeaderClan from './leadersClan/leaderClan';
import ImageLib from './library/imageLib';
import PageTitle from '../../components/page-title/PageTitle';
import Wheel from '../../components/wheel/Wheel';
import { connect } from 'react-redux';
import language from '../../modules/language';
// import { url } from 'inspector';

class Introduction extends React.Component {
    constructor(props) {
        super(props);
        this.onScroll = _.throttle(this._onScroll, 50);
        this.preventMoveNextPageFlag = false;
        this.state = {
            currentPage: 0,
            isFocus: false,
        }
        this.lastScrollTime = Date.now();
        this.scrollCount = 0;
        this.threshold = 1;
        this.moveNextPart = _.throttle(this._moveNextPart, 3000,
            { leading: true, trailing: false });
        this.deltaY = 0;
        this.maxPage = 7;
        this.timeoutPart = {};

        this.preventMoveNextPageFlagResetInterval = setInterval(()=>{
        this.preventMoveNextPageFlag = false;
        },1000);
    }
    myRef = React.createRef()
    selectBoxPagi(index) {
        if (index !== this.state.currentPage) {
            this.setState({ currentPage: index });

        }

    }


    // componentDidMount(){

    // }

    componentDidMount() {
        window.onwheel = this.onScroll; // modern standard       
        setTimeout(() => {
            this.setState({ isFocus: true })
        }, 1000);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.onScroll);
        clearInterval(this.preventMoveNextPageFlagResetInterval);
    }

    preventMoveNextPage = ()=>{
        this.preventMoveNextPageFlag = true;
    }


    _onScroll = e => {
        if(this.preventMoveNextPageFlag){
            return;
        }
        if (Date.now() - this.lastScrollTime < 150) {
            this.scrollCount++;
            this.deltaY += e.deltaY;
            if (this.scrollCount > this.threshold) {
                this.scrollCount = 0;
                this.moveNextPart(this.deltaY < 0);
                this.deltaY = 0;
            }
        } else {
            this.scrollCount = 0;
            this.deltaY = 0;
        }
        this.lastScrollTime = Date.now();
    }

    _moveNextPart = (reverse) => {
        const delta = reverse ? -1 : 1;
        let newPage = this.state.currentPage + delta;
        if (newPage < 0) {
            newPage = 0;
        } else if (newPage >= this.maxPage) {
            newPage = 0;
        }

        this.moveToPart(newPage);
    }

    moveToPart(newPage) {
        this.setState({
            currentPage: newPage,
        });

        //focus page
        for (let i = 0; i < this.maxPage; i++) {
            const part = this['part' + i];
            if (this.timeoutPart[i]) {
                clearTimeout(this.timeoutPart[i]);
            }

            if (i === newPage) {
                this.timeoutPart[i] = setTimeout(() => {
                    if (part && part.setFocus) {
                        part.setFocus(true);
                    }
                    this.timeoutPart[i] = undefined;
                }, 1000);
            } else {
                this.timeoutPart[i] = setTimeout(() => {
                    if (part && part.setFocus) {
                        part.setFocus(false);
                    }
                    this.timeoutPart[i] = undefined;
                }, 0);
            }
        }
    }

    render() {
        const { currentPage } = this.state;
        return (
            <div style={{ overflow: 'hidden', height: '100vh' }}>
                <BoxGination data={Array.from(Array(this.maxPage).keys(), i => i + 1)}
                    currentIndex={currentPage}
                    onChange={(page) => this.moveToPart(page)}
                />
                <PageTitle>{language.t("about_page")}</PageTitle>
                <Wheel />
                <AboutUs
                    style={{
                        transition: 'all 2s',
                        height: currentPage == 0 ? '100vh' : '0px',
                        overflow: 'hidden',
                        backgroundImage: `url(${aboutUsBgd})`,
                        backgroundPosition: "center",
                        backgroundSize: 'cover',
                        backgroundRepeat: 'no-repeat',
                        display: "flex"
                    }}
                    onPreventMoveNextPage={this.preventMoveNextPage}
                    isFocus={this.state.isFocus && currentPage == 0}
                />
                <DevelopRoad
                    style={{
                        transition: 'all 2s',
                        height: currentPage == 1 ? '100vh' : '0px',
                        overflow: 'hidden',
                        backgroundImage: `url(${developBgr})`,
                        backgroundPosition: "center",
                        backgroundSize: 'cover',
                        backgroundRepeat: 'no-repeat',
                        display: "flex"
                    }}
                    ref={ref => this.part1 = ref}
                    isFocus={this.state.isFocus && currentPage == 1}
                />
                <RealValue
                    style={{
                        transition: 'all 2s',
                        height: currentPage == 2 ? '100vh' : '0px',
                        overflow: 'hidden',
                        backgroundImage: `url(${realValBgr})`,
                        backgroundPosition: "center",
                        backgroundSize: 'cover',
                        backgroundRepeat: 'no-repeat',
                        display: "flex"
                    }}
                    onPreventMoveNextPage={this.preventMoveNextPage}
                    isFocus={this.state.isFocus && currentPage == 2}
                />
                <StructCompany
                    style={{
                        transition: 'all 2s',
                        height: currentPage == 3 ? '100vh' : '0px',
                        overflow: 'hidden',
                        backgroundImage: `url(${realValBgr})`,
                        backgroundPosition: "center",
                        backgroundSize: 'cover',
                        backgroundRepeat: 'no-repeat',
                        display: "flex"
                    }}
                    isFocus={this.state.isFocus && currentPage == 3}
                />
                <LeaderClan
                    style={{
                        transition: 'all 2s',
                        height: currentPage == 4 ? '100vh' : '0px',
                        overflow: 'hidden',
                        backgroundImage: `url(${managerBgr})`,
                        backgroundPosition: "center",
                        backgroundSize: 'cover',
                        backgroundRepeat: 'no-repeat',
                        display: "flex"
                    }}
                    isFocus={this.state.isFocus && currentPage == 4}
                    onPreventMoveNextPage={this.preventMoveNextPage}
                />
                <Archieve
                    style={{
                        transition: 'all 2s',
                        height: currentPage == 5 ? '100vh' : '0px',
                        overflow: 'hidden',
                        backgroundImage: `url(${archieveBgr})`,
                        backgroundPosition: "center",
                        backgroundSize: 'cover',
                        backgroundRepeat: 'no-repeat',
                        display: "flex"
                    }}

                    isFocus={this.state.isFocus && currentPage == 5}
                />
                <ImageLib
                    style={{
                        transition: 'all 2s',
                        height: currentPage == 6 ? '100vh' : '0px',
                        overflow: 'hidden',
                        backgroundImage: `url(${realValBgr})`,
                        backgroundPosition: "center",
                        backgroundSize: 'cover',
                        backgroundRepeat: 'no-repeat',
                        display: "flex"
                    }}
                    isFocus={this.state.isFocus && currentPage == 6}
                />
            </div>

        )

    }
}

const mapStateToProps = state => ({
    app: state.app
})

export default connect(mapStateToProps, null)(Introduction)