import React from 'react';
import PropTypes from 'prop-types';
import ItemsCarousel from 'react-items-carousel';
import './style.css';
import prev from '../../../assets/images/prev.png';
import next from '../../../assets/images/next.png';
import { connect } from 'react-redux';
import language from '../../../modules/language';
import route from '../../../config/route'
import { Link } from 'react-router-dom'

// import libImg1 from '../../../assets/images/lib-img-1.jpg';
import libImg from '../../../assets/images/lib-img-2.jpg';
// import libImg3 from '../../../assets/images/lib-img-3.jpg';
// import libImg4 from '../../../assets/images/lib-img-4.jpg';
// import libImg5 from '../../../assets/images/lib-img-5.jpg';
// import libImg6 from '../../../assets/images/lib-img-6.jpg';
// import libImg7 from '../../../assets/images/lib-img-7.jpg';

class Archieve extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            focus: false,
            activeItemIndex: 0,
        }

        this.numberItem = Math.floor(window.innerWidth / 350);
        this.maxItem = 7;

        this.data = [
            { image: require('../../../assets/posts/thi-cong-san-du-ung-luc/p4-2.jpg'), title: 'Sàn không đầm', link: route.Posts.SanKhongDam },
            { image: libImg, title: 'BTCT dự ứng với BTCT thông thường', link: route.Posts.btctDuUngLucVsThongThuong },
            { image: require('../../../assets/posts/thi-cong-san-du-ung-luc/p4-1.jpg'), title: 'Thi công sàn dự ứng ', link: route.Posts.ThiCongSanDuUngLucKS },
        ];
        for (let i = 1; i <= 38; i++) {
            const libImg = require('../../../assets/images/thu-vien/' + i + '.jpg');
            this.data.push(
                { image: libImg, title: 'Hình ảnh', link: libImg },
            )
        }
    }

    componentDidMount() {
        this.interval = setInterval(() => {
            this.setState({
                activeItemIndex: this.state.activeItemIndex >= this.maxItem - 1 ?
                    0 : this.state.activeItemIndex + 1,
            });
        }, 150000);
    }

    setFocus(focus) {
        this.setState({ focus })
    }

    render() {
        const { activeItemIndex } = this.state;
        const focus = this.props.isFocus;
        const data = this.data;
        return (
            <div style={{ ...this.props.style }}>
                <div style={{
                    backgroundColor: 'rgba(111,111,111, 0.3)',
                    display: 'flex',
                    width: '100%'
                }}>
                    <div className="archieve-container">
                        <h2
                            style={{
                                transition: 'all 0.8s',
                                opacity: focus ? 1 : 0,
                            }}
                        >Năng lực thi công</h2>
                        <div className="lib-img-contain"
                            style={{
                                transition: 'all 2s',
                                position: 'relative',
                                bottom: focus ? '0' : '-50rem'
                            }}
                        >
                            <ItemsCarousel
                                requestToChangeActive={
                                    (item) => {
                                        this.setState({ activeItemIndex: item })
                                    }
                                }
                                activeItemIndex={activeItemIndex}
                                numberOfCards={this.numberItem}
                                gutter={20}
                                leftChevron={<div className="lib-chevron"
                                > <img width="50%" height="50%" style={{ margin: "auto" }} src={prev} /></div>}
                                rightChevron={<div className="lib-chevron"
                                > <img width="50%" height="50%" style={{ margin: "auto" }} src={next} /></div>}
                                slidesToScroll={1}
                            >
                                {data.map((item, index) => {
                                    const content = <div className="lib-img-item" style={{
                                    }}>
                                        <div style={{ width: '100%', position: 'relative', paddingBottom: '100%' }}>
                                            <img src={item.image} width="100%" height="100%" style={{ position: 'absolute' }}></img>
                                        </div>
                                        {typeof item.title === 'string' && <div
                                            style={{
                                                width: "100%",
                                                backgroundColor: "#fff",
                                                borderRadius: "10px",
                                                marginTop: "10px",
                                                color: "#000",
                                                textAlign: "center",
                                                padding: "10px"
                                            }}
                                        >
                                            <span>
                                                {item.title}
                                            </span>
                                        </div>}
                                    </div>;
                                    if (item.link) {
                                        return <Link to={item.link} target={'_blank'}>
                                            {content}
                                        </Link>
                                    }
                                    return content
                                }

                                )}
                            </ItemsCarousel>
                        </div>
                    </div>
                </div>
            </div>

        )
    }

}

Archieve.propTypes = {
    setFocus: PropTypes.func
}

const mapStateToProps = state => ({
    app: state.app
})

export default connect(mapStateToProps, null)(Archieve)